<template>
  <div class="w-100 px-0">
    <teleport to="#breadcrumb">
      <ul class="navbar-nav flex-row">
        <li>
          <div class="page-header">
            <nav class="breadcrumb-one" aria-label="breadcrumb">
              <ol class="breadcrumb">
                <li class="breadcrumb-item"><a href="javascript:;">Charts</a></li>
                <li class="breadcrumb-item active" aria-current="page"><span>Apex</span></li>
              </ol>
            </nav>
          </div>
        </li>
      </ul>
    </teleport>

    <div v-scroll-spy class="row g-0 my-4">
      <!-- Key Metrics Section -->
      <div class="col-12 mb-4">
        <div class="row ms-0 me-0">
          <!-- Total Tasks -->
          <div class="col-lg-2 col-md-4 col-sm-6 mb-3">
            <div class="statbox2 panel box box-shadow">
              <div class="panel-body p-3">
                <div class="d-flex justify-content-between align-items-center">
                  <div>
                    <h6 class="mb-1">Total Tasks</h6>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!-- Total Approved Budget -->
          <div class="col-lg-2 col-md-4 col-sm-6 mb-3">
            <div class="statbox2 panel box box-shadow">
              <div class="panel-body p-3">
                <div class="d-flex justify-content-between align-items-center">
                  <div>
                    <h6 class="mb-1">Total Approved Budget</h6>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!-- Budget Consumed -->
          <div class="col-lg-2 col-md-4 col-sm-6 mb-3">
            <div class="statbox2 panel box box-shadow">
              <div class="panel-body p-3">
                <div class="d-flex justify-content-between align-items-center">
                  <div>
                    <h6 class="mb-1">Budget Consumed</h6>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!-- Remaining Budget -->
          <div class="col-lg-2 col-md-4 col-sm-6 mb-3">
            <div class="statbox2 panel box box-shadow">
              <div class="panel-body p-3">
                <div class="d-flex justify-content-between align-items-center">
                  <div>
                    <h6 class="mb-1">Remaining Budget</h6>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!-- Amount Paid -->
          <div class="col-lg-2 col-md-4 col-sm-6 mb-3">
            <div class="statbox2 panel box box-shadow">
              <div class="panel-body p-3">
                <div class="d-flex justify-content-between align-items-center">
                  <div>
                    <h6 class="mb-1">Amount Paid</h6>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!-- Amount Due -->
          <div class="col-lg-2 col-md-4 col-sm-6 mb-3">
            <div class="statbox2 panel box box-shadow">
              <div class="panel-body p-3">
                <div class="d-flex justify-content-between align-items-center">
                  <div>
                    <h6 class="mb-1">Amount Due</h6>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="row ms-0 me-0">
          <!-- Total Tasks Value -->
          <div class="col-lg-2 col-md-4 col-sm-6 mb-3">
            <div class="statbox panel box box-shadow">
              <div class="panel-body p-3">
                <div class="d-flex justify-content-between align-items-center">
                  <div>
                    <h3 class="mb-0">{{ metrics.totalTasks || 0 }}</h3>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!-- Total Approved Budget Value -->
          <div class="col-lg-2 col-md-4 col-sm-6 mb-3">
            <div class="statbox panel box box-shadow">
              <div class="panel-body p-3">
                <div class="d-flex justify-content-between align-items-center">
                  <div>
                    <h3 class="mb-0">{{ formatNumber(metrics.totalApprovedBudget || 0) }}</h3>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!-- Budget Consumed Value -->
          <div class="col-lg-2 col-md-4 col-sm-6 mb-3">
            <div class="statbox panel box box-shadow">
              <div class="panel-body p-3">
                <div class="d-flex justify-content-between align-items-center">
                  <div>
                    <h3 class="mb-0">{{ formatNumber(metrics.budgetConsumed || 0) }}</h3>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!-- Remaining Budget Value -->
          <div class="col-lg-2 col-md-4 col-sm-6 mb-3">
            <div class="statbox panel box box-shadow">
              <div class="panel-body p-3">
                <div class="d-flex justify-content-between align-items-center">
                  <div>
                    <h3 class="mb-0">{{ formatNumber(metrics.remainingBudget || 0) }}</h3>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!-- Amount Paid Value -->
          <div class="col-lg-2 col-md-4 col-sm-6 mb-3">
            <div class="statbox panel box box-shadow">
              <div class="panel-body p-3">
                <div class="d-flex justify-content-between align-items-center">
                  <div>
                    <h3 class="mb-0">{{ formatNumber(metrics.amountPaid || 0) }}</h3>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!-- Amount Due Value -->
          <div class="col-lg-2 col-md-4 col-sm-6 mb-3">
            <div class="statbox panel box box-shadow">
              <div class="panel-body p-3">
                <div class="d-flex justify-content-between align-items-center">
                  <div>
                    <h3 class="mb-0">{{ formatNumber(metrics.amountDue || 0) }}</h3>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <!-- Row 1: Task Breakdown & Approved Budget Charts -->
      <div class="col-12 mb-4">
        <div class="row ms-0 me-0">
          <!-- Task Breakdown by Status -->
          <div class="col-lg-6 col-md-12 mb-4">
            <div class="statbox panel box box-shadow h-100">
              <div class="panel-heading">
                <h5 class="text-center mb-0">Task Breakdown by Status</h5>
              </div>
              <div class="panel-body">
                <apexchart
                  v-if="taskBreakdownOptions"
                  height="450"
                  type="donut"
                  :options="taskBreakdownOptions"
                  :series="taskBreakdownSeries"
                />
              </div>
            </div>
          </div>

          <!-- Approved Budget For Each Category -->
          <div class="col-lg-6 col-md-12 mb-4">
            <div class="statbox panel box box-shadow h-100">
              <div class="panel-heading">
                <h5 class="text-center mb-0">Approved Budget For Each Category</h5>
              </div>
              <div class="panel-body">
                <apexchart
                  v-if="approvedBudgetByCategoryOptions"
                  height="450"
                  type="bar"
                  :options="approvedBudgetByCategoryOptions"
                  :series="approvedBudgetByCategorySeries"
                />
              </div>
            </div>
          </div>
        </div>
      </div>

      <!-- Row 2: Status Overview & Project Progress -->
      <div class="col-12 mb-4">
        <div class="row ms-0 me-0">
          <!-- Status Overview By Project Phase -->
          <div class="col-lg-6 col-md-12 mb-4">
            <div class="statbox panel box box-shadow h-100">
              <div class="panel-heading">
                <h5 class="text-center mb-0">Status Overview By Project Phase</h5>
              </div>
              <div class="panel-body">
                <apexchart
                  v-if="statusOverviewOptions"
                  height="350"
                  type="bar"
                  :options="statusOverviewOptions"
                  :series="statusOverviewSeries"
                />
              </div>
            </div>
          </div>

          <!-- Overall Project Progress (Pie Chart) -->
          <div class="col-lg-6 col-md-12 mb-4">
            <div class="statbox panel box box-shadow h-100">
              <div class="panel-heading">
                <h5 class="text-center mb-0">Overall Project Progress</h5>
              </div>
              <div class="panel-body">
                <div class="text-center">
                  <apexchart
                    v-if="projectProgressOptions"
                    height="450"
                    type="pie"
                    :options="projectProgressOptions"
                    :series="projectProgressSeries"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <!-- Row 3: Budget Analysis & Expense Analysis -->
      <div class="col-12">
        <div class="row ms-0 me-0">   
          <!-- Budget Analysis: Approved vs. Consumed by Category -->
          <div class="col-lg-6 col-md-12 mb-4">
            <div class="statbox panel box box-shadow h-100">
              <div class="panel-heading">
                <h5 class="text-center mb-0">Budget Analysis: Approved vs. Consumed by Category</h5>
              </div>
              <div class="panel-body">
                <apexchart
                  v-if="budgetVsConsumedOptions"
                  height="450"
                  type="bar"
                  :options="budgetVsConsumedOptions"
                  :series="budgetVsConsumedSeries"
                />
              </div>
            </div>
          </div>
          
          <!-- Expense Analysis: Paid Versus Due by Category (Updated to Horizontal Bars) -->
          <div class="col-lg-6 col-md-12 mb-4">
            <div class="statbox panel box box-shadow h-100">
              <div class="panel-heading">
                <h5 class="text-center mb-0">Expense Analysis: Paid Versus Due by Category</h5>
              </div>
              <div class="panel-body">
                <apexchart
                  v-if="paidVsDueOptions"
                  height="450"
                  type="bar"
                  :options="paidVsDueOptions"
                  :series="paidVsDueSeries"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted, inject } from 'vue';
import '@/assets/sass/scrollspyNav.scss';
import apexchart from 'vue3-apexcharts';
import { useStore } from 'vuex';
import { useMeta } from '@/composables/use-meta';
import axios from 'axios';
import Multiselect from '@suadelabs/vue3-multiselect';
import '@suadelabs/vue3-multiselect/dist/vue3-multiselect.css';

useMeta({ title: 'Apex Chart' });
const formatNumber = inject('formatNumber');
const showMessage = inject('showMessage');
const showError = inject('showError');
const store = useStore();

const metrics = ref({
  totalTasks: 0,
  totalApprovedBudget: 0,
  budgetConsumed: 0,
  remainingBudget: 0,
  amountPaid: 0,
  amountDue: 0
});

const statusOverviewSeries = ref([
  { name: 'Delayed', data: [20, 30, 10, 40] },
  { name: 'Complete', data: [40, 30, 50, 20] },
  { name: 'Ongoing', data: [30, 20, 30, 30] },
  { name: 'Cancelled', data: [10, 10, 5, 5] },
  { name: 'Billed', data: [0, 10, 5, 5] }
]);

const projectProgressSeries = ref([65, 35]); 

const budgetVsConsumedSeries = ref([
  { name: 'Approved Budget', data: [100000, 150000, 80000, 120000, 90000] },
  { name: 'Consumed Budget', data: [75000, 120000, 60000, 90000, 70000] }
]);

const paidVsDueSeries = ref([
  { name: 'Amount Paid', data: [50000, 80000, 40000, 60000, 45000, 35000, 28000, 42000, 55000, 38000] },
  { name: 'Amount Due', data: [25000, 40000, 20000, 30000, 25000, 15000, 12000, 18000, 22000, 16000] }
]);

const taskBreakdownSeries = ref([15, 35, 30, 5, 15]); 

const approvedBudgetByCategorySeries = ref([
  {
    name: 'Approved Budget',
    data: [
      850000,   
      1200000,  
      450000,   
      150000,   
      75000,    
      250000,   
      120000,   
      180000,   
      350000,   
      220000    
    ]
  }
]);

const approvedBudgetByCategoryCategories = ref([
  'Labor Costs',
  'Material Costs',
  'Equipment Rental',
  'Insurance',
  'Permits and Lic...',
  'Utility Costs',
  'Safety Equipment',
  'Site Security',
  'Project Manag...',
  'Transportation...'
]);

const paidVsDueCategories = ref([
  'Labor',
  'Materials',
  'Equipment',
  'Subcontractors',
  'Overhead',
  'Utilities',
  'Insurance',
  'Permits',
  'Transportation',
  'Miscellaneous'
]);




// Chart Options

// Status Overview By Project Phase (Stacked Bar Chart)
const statusOverviewOptions = computed(() => {
  const is_dark = store.state.is_dark_mode;
  return {
    chart: {
      type: 'bar',
      stacked: true,
      toolbar: { show: false },
      fontFamily: 'inherit',
      height: 350
    },
    plotOptions: {
      bar: {
        horizontal: false,
        columnWidth: '60%',
        borderRadius: 4
      }
    },
    colors: ['#8172BC', '#289742', '#3579BE', '#C43920', '#61D4F5'],
    labels: ['Delayed', 'Complete', 'Ongoing', 'Cancelled', 'Billed'],
    xaxis: {
      categories: ['Planning', 'Design', 'Construction', 'Completion'],
      labels: {
        style: {
          colors: is_dark ? '#888ea8' : '#3b3f5c',
          fontSize: '12px'
        }
      }
    },
    yaxis: {
      labels: {
        style: {
          colors: is_dark ? '#888ea8' : '#3b3f5c',
          fontSize: '12px'
        },
        formatter: function(val) {
          return Math.round(val);
        }
      }
    },
    legend: {
      position: 'bottom',
      fontSize: '12px',
      labels: {
        colors: is_dark ? '#888ea8' : '#3b3f5c'
      }
    },
    tooltip: {
      theme: is_dark ? 'dark' : 'light',
      y: {
        formatter: function(val) {
          return Math.round(val);
        }
      }
    },
    grid: {
      borderColor: is_dark ? '#191e3a' : '#e0e6ed',
      strokeDashArray: 3
    },
    dataLabels: {
      enabled: false
    }
  };
});

// Overall Project Progress (Pie Chart)
const projectProgressOptions = computed(() => {
  const is_dark = store.state.is_dark_mode;
  return {
    chart: {
      type: 'pie',
      fontFamily: 'inherit'
    },
    labels: ['Completed', 'Remaining'],
    colors: ['#00883B', '#ECECEC'],
    legend: {
      position: 'bottom',
      fontSize: '12px',
      labels: {
        colors: is_dark ? '#888ea8' : '#3b3f5c'
      }
    },
    dataLabels: {
      enabled: true,
      formatter: function(val) {
        return val.toFixed(1) + '%';
      },
      style: {
        fontSize: '12px',
        colors: ['#fff'] 
      }
    },
    tooltip: {
      theme: is_dark ? 'dark' : 'light',
      y: {
        formatter: function(val) {
          return val.toFixed(1) + '%';
        }
      }
    },
    plotOptions: {
      pie: {
        donut: {
          size: '0%'
        },
        expandOnClick: true
      }
    },
    responsive: [{
      breakpoint: 480,
      options: {
        chart: {
          width: 200
        },
        legend: {
          position: 'bottom'
        }
      }
    }]
  };
});

// Budget Analysis: Approved vs. Consumed by Category (Grouped Bar Chart)
const budgetVsConsumedOptions = computed(() => {
  const is_dark = store.state.is_dark_mode;
  return {
    chart: {
      type: 'bar',
      toolbar: { show: false },
      fontFamily: 'inherit',
      height: 350
    },
    plotOptions: {
      bar: {
        horizontal: false,
        columnWidth: '55%',
        borderRadius: 4
      }
    },
    colors: ['#30691C', '#FDD459'],
    xaxis: {
      categories: ['Labor', 'Materials', 'Equipment', 'Subcontractors', 'Overhead'],
      labels: {
        style: {
          colors: is_dark ? '#888ea8' : '#3b3f5c',
          fontSize: '12px'
        }
      }
    },
    yaxis: {
      labels: {
        style: {
          colors: is_dark ? '#888ea8' : '#3b3f5c',
          fontSize: '12px'
        },
        formatter: function(val) {
          return formatNumber(val);
        }
      }
    },
    legend: {
      position: 'bottom',
      fontSize: '12px',
      labels: {
        colors: is_dark ? '#888ea8' : '#3b3f5c'
      }
    },
    tooltip: {
      theme: is_dark ? 'dark' : 'light',
      y: {
        formatter: function(val) {
          return formatNumber(val);
        }
      }
    },
    grid: {
      borderColor: is_dark ? '#191e3a' : '#e0e6ed',
      strokeDashArray: 3
    },
    dataLabels: {
      enabled: false
    }
  };
});

// Expense Analysis: Paid Versus Due by Category (Horizontal Bar Chart)
const paidVsDueOptions = computed(() => {
  const is_dark = store.state.is_dark_mode;
  const maxValue = Math.max(
    ...paidVsDueSeries.value[0].data,
    ...paidVsDueSeries.value[1].data
  );

  return {
    chart: {
      type: 'bar',
      toolbar: { show: false },
      fontFamily: 'inherit',
      height: 350
    },

    plotOptions: {
      bar: {
        horizontal: true,
        barHeight: '70%',
        borderRadius: 4
      }
    },

    colors: ['#3478E3', '#C73822'], // Paid (Blue) | Due (Red)

    dataLabels: {
      enabled: false
    },

    xaxis: {
      categories: paidVsDueCategories.value,
      max: maxValue * 1.25, // right padding for clean spacing
      labels: {
        show: true,
        style: {
          colors: is_dark ? '#888ea8' : '#3b3f5c',
          fontSize: '11px'
        },
        formatter: function (value) {
          return formatNumber(value);
        }
      },
      axisBorder: { show: false },
      axisTicks: { show: false }
    },

    yaxis: {
      labels: {
        show: true,
        style: {
          colors: is_dark ? '#888ea8' : '#3b3f5c',
          fontSize: '11px'
        },
        maxWidth: 140
      },
      axisBorder: { show: false },
      axisTicks: { show: false }
    },

    grid: {
      borderColor: is_dark ? '#191e3a' : '#e0e6ed',
      strokeDashArray: 3,
      xaxis: {
        lines: { show: true }
      },
      yaxis: {
        lines: { show: false }
      }
    },

    tooltip: {
      theme: is_dark ? 'dark' : 'light',
      shared: true,
      intersect: false,
      y: {
        formatter: function (val, { seriesIndex, dataPointIndex, w }) {
          const seriesName = w.globals.seriesNames[seriesIndex];
          return seriesName + ': ' + formatNumber(val);
        }
      },
      x: {
        show: true,
        formatter: function (val, { dataPointIndex, w }) {
          return paidVsDueCategories.value[dataPointIndex];
        }
      }
    },

    fill: {
      opacity: 1
    },

    legend: {
      position: 'bottom',
      fontSize: '12px',
      labels: {
        colors: is_dark ? '#888ea8' : '#3b3f5c'
      }
    },

    responsive: [
      {
        breakpoint: 768,
        options: {
          chart: {
            height: 400
          },
          xaxis: {
            labels: {
              style: {
                fontSize: '9px'
              }
            }
          },
          yaxis: {
            labels: {
              style: {
                fontSize: '9px'
              },
              maxWidth: 90
            }
          }
        }
      }
    ]
  };
});


// Task Breakdown by Status (Donut Chart)
const taskBreakdownOptions = computed(() => {
  const is_dark = store.state.is_dark_mode;
  return {
    chart: {
      type: 'donut',
      fontFamily: 'inherit'
    },
    labels: ['Delayed', 'Complete', 'Ongoing', 'Cancelled', 'Billed'],
    colors: ['#8172BC', '#289742', '#3579BE', '#C43920', '#61D4F5'],
    legend: {
      position: 'bottom',
      fontSize: '12px',
      labels: {
        colors: is_dark ? '#888ea8' : '#3b3f5c'
      }
    },
    dataLabels: {
      enabled: true,
      formatter: function(val) {
        return val.toFixed(1) + '%';
      },
      style: {
        fontSize: '12px',
        colors: ['#fff']
      }
    },
    responsive: [{
      breakpoint: 480,
      options: {
        chart: {
          width: 200
        },
        legend: {
          position: 'bottom'
        }
      }
    }]
  };
});


// Approved Budget For Each Category (Horizontal Bar Chart)
const approvedBudgetByCategoryOptions = computed(() => {
  const is_dark = store.state.is_dark_mode;
  const maxBudget = Math.max(...approvedBudgetByCategorySeries.value[0].data);

  return {
    chart: {
      type: 'bar',
      toolbar: { show: false },
      fontFamily: 'inherit'
    },

    plotOptions: {
      bar: {
        horizontal: true,
        barHeight: '70%',
        borderRadius: 4
      }
    },

    colors: ['#009E92'],

    dataLabels: {
      enabled: false
    },

    xaxis: {
      categories: approvedBudgetByCategoryCategories.value,
      max: maxBudget * 1.15,
      labels: {
        show: true,
        style: {
          colors: is_dark ? '#888ea8' : '#3b3f5c',
          fontSize: '11px'
        },
        formatter: function(val) {
          return formatNumber(val);
        }
      },
      axisBorder: { show: false },
      axisTicks: { show: false }
    },

    yaxis: {
      labels: {
        show: true,
        style: {
          colors: is_dark ? '#888ea8' : '#3b3f5c',
          fontSize: '11px'
        },
        maxWidth: 140
      },
      axisBorder: { show: false },
      axisTicks: { show: false }
    },

    grid: {
      borderColor: is_dark ? '#191e3a' : '#e0e6ed',
      strokeDashArray: 3,
      xaxis: {
        lines: { show: true }
      },
      yaxis: {
        lines: { show: false }
      }
    },

    tooltip: {
      theme: is_dark ? 'dark' : 'light',
      y: {
        formatter: function (val) {
          return formatNumber(val);
        },
        title: {
          formatter: function (seriesName) {
            return seriesName;
          }
        }
      },
      x: {
        show: true,
        formatter: function (val, { dataPointIndex }) {
          return approvedBudgetByCategoryCategories.value[dataPointIndex];
        }
      }
    },

    fill: {
      opacity: 1
    },

    legend: {
      show: false
    },

    responsive: [
      {
        breakpoint: 768,
        options: {
          chart: {
            height: 500
          },
          yaxis: {
            labels: {
              maxWidth: 90
            }
          }
        }
      }
    ]
  };
});

</script>

<style scoped>
.statbox {
  border-radius: 8px;
  border: 1px solid #e0e6ed;
}

.statbox2 {
  background-color: #1b2e4b;
  border-radius: 8px;
  border: 1px solid #e0e6ed;
}

.panel-heading {
  padding: 1rem;
  border-bottom: 1px solid #e0e6ed;
}

.panel-body {
  padding: 1rem;
}

h3 {
  font-weight: 600;
}

h5 {
  font-weight: 600;
}

h6 {
  font-size: 0.875rem;
  font-weight: 600;
  color: white;
}

.small {
  font-size: 0.875rem;
  color: #6c757d;
  font-weight: 500;
}

/* Dark mode support */
:deep(.apexcharts-tooltip) {
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

:deep(.apexcharts-text) {
  font-family: inherit !important;
}

/* Ensure consistent chart heights */
:deep(.apexcharts-canvas) {
  margin: 0 auto;
}

/* Additional styles for horizontal bar charts */
